.kudo {
  display: flex;
  align-items: center;
  cursor: pointer; }
  .kudo.complete .kudobject .opening .circle {
    transition-property: none;
    animation: bounce 1s;
    animation-iteration-count: 1;
    transform: scale(0.85); }
  .kudo.active .opening .circle {
    transform: scale(1.1);
    background-color: rgba(0, 0, 0, 0.7); }
  .kudo.complete .opening .circle {
    background-position: center;
    background-repeat: no-repeat;
    background-color: #4d4f50;
    background-size: 80%; }
  .kudo .opening {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #fff;
    border: 2px solid #4d4f50;
    position: relative; }
    .kudo .opening .circle {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-left: -25px;
      margin-top: -25px;
      background-color: #4d4f50;
      transition-property: transform, background-color;
      transition-duration: 1s;
      transition-timing-function: ease-out;
      transform: scale(0.3);
      transition-style: preserve-3d;
      backface-visibility: visible; }
  .kudo .count {
    margin-left: 1em;
    color: #999b9d;
    font-size: 12px; }

@keyframes bounce {
  0% {
    transform: scale(1.1);
    background-color: #000;
    opacity: 1; }
  50% {
    transform: scale(1.6);
    opacity: .7;
    background-color: #000; }
  60% {
    transform: scale(0.6);
    opacity: 1; }
  80% {
    transform: scale(0.95); }
  100% {
    transform: scale(0.85); } }

